export default class UserInfo {
  constructor({ profileName, profileJob, profileAvatar }) {
    this._name = document.querySelector(profileName);
    this._job = document.querySelector(profileJob);
    this._avatar = document.querySelector(profileAvatar);
    
  }

  getUserInfo() {
    return {
      userName: this._name.textContent,
      userDescription: this._job.textContent
    }
  }

  setUserInfo({ userName, userDescription, avatar }) {
    this._name.textContent = userName;
    this._job.textContent = userDescription;
  }

  setUserAvatar({ userAvatarLink }) {
    this._avatar.src = userAvatarLink;
  }

  saveUserId(userId) {
    this._userId = userId;
  }

  getUserId() {
    return this._userId;
  }
}